export const VariableButton = <C extends ElementType>(
  props: ButtonProps<C, { component?: C }> | any
) => {
  const {
    children,
    type = "button",
    color = "inherit",
    icon,
    sx,
    ...rest
  } = props;
  return (
    <Button
      type={type}
      color={color}
      sx={
        color === "inherit"
          ? {
              ...VariableButtonSX,
            }
          : sx
      }
      {...rest}
    >
      {children}
    </Button>
  );
};

