export const ActionButton = <C extends ElementType>(
  props: ButtonProps<C, { component?: C }> | any
) => {
  const {
    children,
    type = "button",
    color = "inherit",
    variant,
    disableIcon = false,
    disabled,
    icon,
    sx,
    ...rest
  } = props;
  return (
    <Button
      type={type}
      color={color}
      variant={variant}
      disabled={disabled}
      sx={
        variant !== "outlined"
          ? {
              ...(color === "inherit"
                ? { ...ActionButtonSX }
                : { ...ActionButtonColoredSX }),
              ...sx,
            }
          : { ...sx }
      }
      {...rest}
    >
      {children}
      {!disableIcon && (
        <Fragment>
          {icon ? (
            icon
          ) : (
            <ArrowRight color={disabled ? "#00000042" : "#ffffffff"} />
          )}
        </Fragment>
      )}
    </Button>
  );
};

